        filteredDeviceTableData() {
            if (!this.selectedTreeNode) {
                // 默认显示所有设备
                return this.deviceTableData;
            }

            const node = this.selectedTreeNode;

            // 如果点击的是叶子节点（具体设备）
            if (node.type === 'chilledWaterPump' || node.type === 'hotWaterPump') {
                return this.deviceTableData.filter(device => device.type === node.type);
            }

            // 如果点击的是机组节点
            if (node.type === 'chilledWaterUnit') {
                return this.deviceTableData.filter(device => device.type === 'chilledWaterPump');
            }

            if (node.type === 'hotWaterUnit') {
                return this.deviceTableData.filter(device => device.type === 'hotWaterPump');
            }

            // 如果点击的是根节点，显示所有设备
            return this.deviceTableData;
        }
